/* Page Css */
.top_header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  -webkit-transition: 0.35s ease-in;
  -o-transition: 0.35s ease-in;
  transition: 0.35s ease-in;
}
.top_header.active-bg{
  background-color: #ffffff;
  -webkit-box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
          box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.navbar-nav .nav-item{
  margin-left: 20px;
}
.navbar-nav .nav-item .nav-link{
  color: #ffffff;
  -webkit-transition: 0.35s ease-in;
  -o-transition: 0.35s ease-in;
  transition: 0.35s ease-in;
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
}
.sub_nav{
  list-style: none;
}
.sub_nav li{
  position: relative;   
}
.sub_nav li::before{
  content: '';
  position: absolute;
  top: 14px;
  left: 0;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: var(--color-green);
}
.sub_nav a{
  -webkit-transition: 0.35s ease-in;
  -o-transition: 0.35s ease-in;
  transition: 0.35s ease-in;
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-size: 15px;
}
.custom_dropdown{
  padding: 20px;
  border: 0;
  border-radius: 0;
  -webkit-box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
          box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  margin-top: -5px;
}
.navbar-nav .nav-item .nav-link::before{
  position: absolute;
  content: '';
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 0;
  height: 2px;
  background-color: var(--color-green);
  -webkit-transition: 0.35s ease-in;
  -o-transition: 0.35s ease-in;
  transition: 0.35s ease-in;
}
.navbar-nav .nav-item .nav-link:hover:before,.navbar-nav .nav-item .nav-link.active:before{
  width: 100%;
}
.navbar-nav .nav-item.active .nav-link,.navbar-nav .nav-item .nav-link:hover{
  color: var(--color-green);
}
.dropdown-item.active, .dropdown-item:active{
  background-color: var(--color-green);
}
.top_header.active-bg .navbar-nav .nav-item .nav-link{
  color: var(--black-primary);
}
.top_header.active-bg .navbar{
  padding-top: 0;
  padding-bottom: 0;
}
.top_header .navbar .navbar-brand img{
  -webkit-transition: 0.35s ease-in-out;
  -o-transition: 0.35s ease-in-out;
  transition: 0.35s ease-in-out;
}
.top_header.active-bg .navbar .navbar-brand img{
  width: 135px;
}
.nav-link.navBtn,.btn-common{
  background-color: var(--color-green);
  border-radius: 0;
  padding: 10px 35px !important;
  font-weight: bold;
  border: 1px solid transparent;
  -webkit-transition: 0.35s ease-in;
  -o-transition: 0.35s ease-in;
  transition: 0.35s ease-in;
  font-family: "Cormorant", serif !important;
  color: #212529 !important;
  font-size: 20px;
}
.navBtn:hover,.btn-common:hover{
  background-color: transparent;
  border: 1px solid var(--color-green);
  color: var(--color-green) !important;
}
.nav-link.navBtn::before{
  height: 0 !important;
}
